@use "../global/mixins.scss";
@use "../global/variables.scss";

.advantages-with-numbers {
  display: flex;
  background-color: #EEEEEE;
  color: #333333;
}

.advantages-with-numbers__list {
  @include mixins.reset-list;
  display: grid;
  grid-column: 268px;
  column-gap: 50px;
  margin: 0 18px;

  @media (min-width: variables.$tablet-width) {
    grid-template-columns: repeat(3, 256px);
    gap: 0;
    margin: 0;
  }

  @media (min-width: variables.$desktop-width) {
    grid-template-columns: repeat(3, 400px);
    gap: 0;
    margin: 0;
  }
}

.advantages-with-numbers__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  .advantages__title {
    margin-top: 24px;
    margin-bottom: 20px;
  }
  .advantages__description {
    margin-bottom: 24px;
  }

  @media (min-width: variables.$tablet-width) {
    height: 256px;
    .advantages__title {
      margin-top: 52px;
    }
  }

  @media (min-width: variables.$desktop-width) {
    height: 256px;
    .advantages__title {
      margin-top: 63px;
    }
  }
}

.advantages-with-numbers__item:not(:last-child) {
  border-bottom: 1px solid #D6D6D6;

  @media (min-width: variables.$tablet-width) {
    border: none;
  }

  @media (min-width: variables.$desktop-width) {
    border: none;
  }
}
